	module.exports = {"data":{"markdownRemark":{"html":"<p>The top-voted comments here are a great example of why people are agreeing with <a href=\"http://daringfireball.net/2010/06/whats_fair\">John Gruber's assessment of comments</a>.</p>\n<blockquote>\n<p>What makes DF an efficient and effective soapbox is exactly that it is <em>not</em> noisy.\nMy goal is for not a single wasted word to appear anywhere on any page of the site.</p>\n</blockquote>\n<p>Those comments distract from the article with fiery, irrelevant rants that show the authors didn't even RTFA. The whole point of the article is lost if you start reading them.</p>","fields":{"post":{"title":"This is why we can't have nice things.","link":"http://blogs.hbr.org/bregman/2010/06/why-i-returned-my-ipad.html","timestamp":"2010-06-17T15:08Z","date":"June 17, 2010 3:08pm UTC"}}}},"pathContext":{"slug":"/this-is-why-we-cant-have-nice-things.html"}}

